header.main-header {
  background-color: var(--bg-color);
  width: min(98vw, 425px);
  border-radius: var(--radius);
  padding: 20px 35px;
  font-family: "Kumbh Sans", sans-serif;
  display: flex;
  flex-direction: column;
  gap: 14px;
  justify-content: center;
}

h2 {
  font-size: 2.2rem;
  font-weight: 700;
  color: white;
  line-height: 1;
}

.made-by {
  display: flex;
  gap: 8px;
  align-items: center;
}
.main-header .made-by h3 {
  color: white;
  font-weight: 300;
}

.GithubButton {
  width: 140px;
  display: flex;
  justify-content: center;
  gap: 10px;
  text-decoration: none;
  color: var(--text-color);
  border-radius: var(--radius);
  padding: 7px;
  background-color: hsl(0, 0%, 16%);
  align-items: center;
}

.GithubButton:hover {
  color: var(--accent-color);
  background-color: hsl(0, 0%, 17.5%);
}

.GithubButton svg {
  color: white;
}

.header-buttons {
  display: flex;
  gap: 20px;
}

.preview-button {
  display: none;
}

.preview-button svg {
  margin-right: 7px;
  position: relative;
  top: 3px;
}


@media (max-width: 1070px) {
  .main-header,
  .CVPreview {
    justify-self: center;
  }
  .preview-button {
    display: block;
  }
}

@media (max-width: 370px) {
  .header-buttons {
    gap: 10px;
  }
  .header-buttons button {
    padding: 10px 18px;
  }
}
